import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import { useDispatch, useSelector } from 'react-redux';
import { getSubTasksThunk, selectCurrentSubTasks, setCurrentTask, setIsModalResult } from '../../../redux/check/checkSlice';
import { useEffect, useState } from 'react';
import ResultTable from '../ResultTable';
import { flushSync } from 'react-dom';

export default function SuccesCard({task}){
    const dispatch = useDispatch()
    const [viewMode, setViewMode] = useState(false)
    const [currentSkip, setCurrentSkip] = useState(0)
    const [currentTake, setCurrentTake] = useState(10)
    const [ filter, setFilter ] = useState(0);
    const currentSubTasks = useSelector(selectCurrentSubTasks)
    const [condSkip, setCondSkip] = useState(task.isValidCount + task.inValidCount + task.notDetected)

    useEffect(()=>{
        dispatch(getSubTasksThunk({id:task.id, skip:currentSkip, take:currentTake, isValid:undefined}))
    }, [])
    useEffect(()=>{                
        setCurrentSkip(0);
        let flag;
        if(filter==0){ 
          setCondSkip(task.isValidCount + task.inValidCount + task.notDetected)
          flag=undefined;
        }
        if(filter==1){
          setCondSkip(task.inValidCount)
           flag=false;
        }
        if(filter==2){ 
          setCondSkip(task.isValidCount)
          flag=true;}
        dispatch(getSubTasksThunk({id:task.id, skip:0, take:currentTake, isValid:flag}))

    }, [filter])
    useEffect(()=>{
        console.log(viewMode)
        if(viewMode){
            let flag;
            if(filter==0){ 
              setCondSkip(task.isValidCount + task.inValidCount + task.notDetected)
              flag=undefined;
            }
            if(filter==1){
              setCondSkip(task.inValidCount)
               flag=false;
            }
            if(filter==2){ 
              setCondSkip(task.isValidCount)
              flag=true;}
            dispatch(getSubTasksThunk({id:task.id, skip:currentSkip, take:currentTake, isValid:flag}))
        }
    },[currentSkip])
    return(
        <Card sx={{ minWidth: 275, borderColor:"rgb(127,255,0,0.5)" }}>
      <CardContent>
        <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
          Последняя обработка
        </Typography>
        <Typography variant="h5" component="div">
          Обработка от {task.time}
        </Typography>
        <Typography sx={{ mb: 1.5 }} color="text.secondary">
          Успешно: {task.isValidCount} Ошибка: {task.inValidCount} Категория не указана: { task.notDetected }
        </Typography>
        <Typography variant="h5" color="rgb(127,255,0)">
          Завершено
          <br />
        </Typography>
      </CardContent>
      <CardActions style={{display:"block"}}>
        {!viewMode && <Button size="small" onClick={()=>{
            dispatch(getSubTasksThunk({id:task.id, skip:currentSkip, take:currentTake}))
            setViewMode(true)
        }}>Посмотреть резульат</Button>}
        {viewMode && <Button size="small" onClick={()=>{
            setViewMode(false)
            setFilter(0);
        }}>Скрыть результаты</Button>}
        {viewMode && <div style={{margin:"1%"}}> 
            <p>Фильтрация:</p>
            <Select labelId="label" id="select" value={filter} onChange={(e)=>{
                setFilter(e.target.value)

            }}>
            <MenuItem value="0">Все</MenuItem>
            <MenuItem value="1">Только ошибочные</MenuItem>
            <MenuItem value="2">Только верные</MenuItem>
          </Select>
          </div>}
        {viewMode && <ResultTable currentSubTasks={currentSubTasks}/>}
        {viewMode && <CardActions >
            {!!currentSkip && <Button size="small" onClick={()=>{
                        setCurrentSkip(currentSkip-10)
                    }}>
                        {"<"}
            </Button>}
            {currentSkip/currentTake+1}
            {(condSkip)>(currentSkip+currentSubTasks.length) && <div>
                <Button size="small" onClick={()=>{
                    setCurrentSkip(currentSkip+10)
                }}>
                    {">"}
                </Button>
            </div>}

        </CardActions>}
        {viewMode && <Button size="small" onClick={()=>{
            setViewMode(false)
            setFilter(0);
        }}>Скрыть результаты</Button>}
      </CardActions>
    </Card>
  );
}